import { Component, EmbeddedViewRef, HostBinding, HostListener, Input, Renderer2, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { animate, keyframes, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'gda-modal',
  template: `
    <ng-template #content>
      <div class="gda-modal-overlay gda-modal-overlay-darked" *ngIf="backdoor && backdoorShow" (click)="closeModal($event)" @overlay></div>
      <div class="gda-modal-container" *ngIf="openStatus" @contentAnimated>
        <ng-content></ng-content>
      </div>
    </ng-template>
  `,
  animations: [
    trigger('overlay', [
      transition(':enter', [
        style({ background: 'rgba(0, 0, 0, 0.5)', 'backdrop-filter': 'blur(3px)' }),
        animate('0.3s ease-in-out',
          keyframes([
            style({
              background: 'rgba(0, 0, 0, 0)', 'backdrop-filter': 'blur(0px)'
            }),
            style({
              background: 'rgba(0, 0, 0, 0.5)', 'backdrop-filter': 'blur(3px)'
            })
          ])
        )
      ]),
      transition(':leave', [
        style({ background: 'rgba(0, 0, 0, 0)', 'backdrop-filter': 'blur(0px)' }),
        animate('0.3s ease-in-out',
          keyframes([
            style({
              background: 'rgba(0, 0, 0, 0.5)', 'backdrop-filter': 'blur(3px)'
            }),
            style({
              background: 'rgba(0, 0, 0, 0)', 'backdrop-filter': 'blur(0px)'
            })
          ])
        )
      ])
    ]),
    trigger('contentAnimated', [
      transition(':enter', [
        style({ transform: 'translateY(0)', opacity: 1 }),
        animate('0.3s ease-in-out',
          keyframes([
            style({ transform: 'translateY(-10px)', opacity: 0 }),
            style({ transform: 'translateY(0)', opacity: 1 }),
          ])
        )
      ]),
      transition(':leave', [
        style({ transform: 'translateY(-10px)', opacity: 0 }),
        animate('0.3s ease-in-out',
          keyframes([
            style({ transform: 'translateY(0)', opacity: 1 }),
            style({ transform: 'translateY(-10px)', opacity: 0 }),
          ])
        )
      ])
    ])
  ]
})
export class GdaModal {
  /**
   * Display
   */
  @HostBinding('style.display') private setStyle = 'none';

  @ViewChild('content', { read: TemplateRef, static: false }) private contentTemplate!: TemplateRef<any>;

  @Input() backdoor: boolean;
  @Input() backdoorNotTriggerClose: boolean;

  @Input() modalId: string;

  @Input() modalClasses: string;

  @Input() escapeEnabled: boolean;

  protected backdoorShow: boolean;
  protected openStatus: boolean;
  private embeddedViewRef: EmbeddedViewRef<any> | null;

  constructor(
    private renderer: Renderer2,
    private viewContainerRef: ViewContainerRef
  ) {
    this.backdoor = true;
    this.backdoorNotTriggerClose = false;
    this.backdoorShow = false;
    this.openStatus = false;
    this.embeddedViewRef = null;
    this.modalId = '';
    this.modalClasses = '';
    this.escapeEnabled = true;
  }

  @HostListener('document:keydown.escape', ['$event']) onKeydownHandler(event: KeyboardEvent) {
    if (this.escapeEnabled) this.close();
  }

  public open(): void {
    if (!this.openStatus) {
      this.openStatus = true;
      this.backdoorShow = true;
      this.embeddedViewRef = this.viewContainerRef.createEmbeddedView(this.contentTemplate);
      this.embeddedViewRef.detectChanges();
      for (const node of this.embeddedViewRef.rootNodes) {
        if (node.classList?.contains('gda-modal-overlay')) {
          const numberContainers: number = document.getElementsByClassName('gda-modal-overlay').length;
          this.renderer.setStyle(node, 'z-index', 1060 + (numberContainers - 1));
          // if (numberContainers <= 1) {
          //   this.renderer.addClass(node, 'gda-modal-overlay-darked');
          // }
        }
        if (node.classList?.contains('gda-modal-container')) {
          if (this.modalId) {
            this.renderer.setProperty(node, 'id', this.modalId);
          }
          if (this.modalClasses) {
            this.modalClasses.split(' ').forEach((c: string) => this.renderer.addClass(node, c));
          }
          const numberContainers: number = document.getElementsByClassName('gda-modal-container').length;
          this.renderer.setStyle(node, 'z-index', 1060 + (numberContainers));
        }
        this.renderer.appendChild(document.body, node);
      }
    }
  }

  public close(e?: Event): void {
    if (e) e.stopPropagation();
    if (this.openStatus) {
      this.openStatus = false;
      this.backdoorShow = false;
      setTimeout(() => {
        if (this.embeddedViewRef) {
          this.embeddedViewRef.destroy();
        }
      }, 300);
    }
  }

  protected closeModal(e: Event): void {
    if (!this.backdoorNotTriggerClose) this.close(e);
  }

}
